@font-face {
  font-family: 'Print Clearly';
  src: local("Print Clearly"), local("Print Clearly"), url("../fonts/Print Clearly.woff2") format("woff2"), url("../fonts/Print Clearly.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

/*    <NULL>    */
* {
  padding: 0;
  margin: 0;
  border: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

*, *:before, *:after {
  box-sizing: border-box;
}

*:focus, *:active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html {
  overflow-x: hidden;
}

html, body {
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: none;
  -webkit-overflow-scrolling: touch;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
  background: transparent;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
  max-width: 100%;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
}

/*    </NULL>    */
/*    <GLOBAL>    */
:root {
  --white: rgba(255,255,255, 1);
  --black: rgba(51,51,51,1);
  --black60: rgba(51,51,51,.6);
  --black30: rgba(51,51,51,.3);
  --black10: rgba(51,51,51, .1);
  --orange: rgba(255,115,36,1);
  --orange90: rgba(255,115,36,.9);
  --orange90: rgba(255,115,36,.9);
  --blue: rgba(28,28,225,1);
  --blue50: rgba(28,28,225,.5);
}

.fix-cont {
  height: 100%;
  width: 100%;
  max-width: 1140px;
  min-width: 320px;
  margin: 0 auto;
}

@media (max-width: 1199px) {
  .fix-cont {
    max-width: 708px;
  }
}

@media (max-width: 768px) {
  .fix-cont {
    max-width: none;
    padding-left: 15px;
    padding-right: 15px;
  }
}

body, a {
  color: var(--black);
}

main {
  margin-top: 130px;
}

.nav-link {
  color: var(--black);
  font-size: 16px;
  line-height: 21px;
}

.nav-link:hover {
  color: var(--orange);
}

.link {
  color: var(--blue);
}

.tag {
  font-family: Segoe UI;
  font-style: normal;
  font-weight: 700;
  font-size: 11px;
  line-height: 15px;
  color: var(--orange);
  text-transform: uppercase;
}

.plain-text {
  font-family: Segoe UI;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 150%;
}

.logo {
  font-family: Print Clearly;
  font-size: 36px;
  line-height: 36px;
}

.social-link, .search-btn, .close-btn {
  background-image: url("../img/sprite.png");
  height: 21px;
}

.social-link {
  display: block;
}

.social-link.tw {
  width: 25.41px;
  background-position: 0 0;
}

.social-link.tw.invert {
  background-position: 0 -26px;
}

.social-link.tw:hover {
  background-position: -86px 0;
}

.social-link.vk {
  width: 37.5px;
  background-position: -30px 0;
}

.social-link.vk.invert {
  background-position: -30px -26px;
}

.social-link.vk:hover {
  background-position: -116px 0;
}

.social-link.fb {
  width: 9.8px;
  background-position: -72px 0;
}

.social-link.fb.invert {
  background-position: -72px -26px;
}

.social-link.fb:hover {
  background-position: -158px 0;
}

.search-btn {
  width: 21px;
  background-position: 0 -52px;
}

.search-btn:hover {
  background-position: -26px -52px;
}

.close-btn {
  width: 21px;
  background-position: -52px -52px;
}

.page-about__fix-cont {
  position: relative;
  padding-left: 15px;
}

.page-about__fix-cont:before {
  content: '';
  width: 100%;
  height: 1px;
  background-color: var(--black30);
  position: absolute;
  bottom: 0;
  left: 0;
}

.page-about__title {
  font-weight: bold;
  font-size: 22px;
  line-height: 100%;
  padding-bottom: 18px;
}

.page-about__article {
  font-size: 18px;
  line-height: 150%;
  width: 735px;
  padding-bottom: 23px;
}

.load-more {
  display: -webkit-box;
